<template>
	<view class="main">
    <!-- 头部begin -->
	<div class="headIcon">
		<icon class="iconfont icon-wodebaobeiye_genghuan" @click="onRefresh"></icon>
		<icon class="iconfont icon-icon-" @click="onScreening"></icon>
	</div>
	<!-- 头部end -->
	
	<!-- 中间卡片begin -->
		<div :class="flag ? 'mesCardAni' : 'mesCard'" @touchmove="handletouchmove" @touchstart="handletouchstart" @touchend="handletouchend" >
			<div class="cardTop">
				<image  class="cardImg" :src="vipMember[i].image"></image>
				<div class="imageNum">
					<div>
						<icon class="iconfont icon-tupian">3</icon>
					</div>
					<div>
						<icon class="iconfont icon-pengyouquan" ></icon>
					</div>
				</div>
			</div>
			<div class="cardBottom">
				<div class="name">{{vipMember[i].name}}</div>
				<div class="other">
					<span><icon class="iconfont icon-nv" style="color: pink;font-size: 22rpx;">{{vipMember[i].age}}</icon></span>
					<span><icon class="iconfont icon-_xingzuoyuncheng" style="color: #fff;font-size: 22rpx;">{{vipMember[i].constellation}}</icon></span>
					<span><icon class="iconfont icon-tianchongxing-" style="color: #fff;font-size: 22rpx;">{{vipMember[i].identity}}</icon></span>
				</div>
				<div class='school'>{{vipMember[i].school}}</div>
			</div>
		</div>
	<!-- 中间卡片end -->
	</view>
</template>

<script>
	export default {
		data() {
			return {
				vipMember:[
					{
						name: '焰灵姬', 
						image: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-miraitowa/17ad87a0-2489-11eb-8ff1-d5dcf8779628.jpg', 
						age: "20",
						constellation: "狮子座",
						identity: '学生',
						school: "秦时明月"
						},
					{
						name: '盖聂', 
						image: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-miraitowa/3fc78fb0-24ac-11eb-8ff1-d5dcf8779628.jpg', 
						age: "22",
						constellation: "天蝎座",
						identity: '老师',
						school: "武汉大学"
						},
				],  //会员信息
				i:0,
				flag: 0,
				lastX: 0,
				lastY: 0			
			}
		},
		onLoad() {
			// uni.request({
			// 	url:"http://81.71.84.252:8090/user/query",
			// 	method:"GET",
			// 	data:{
			// 		page:1,
			// 		rows:10
			// 	},
			// 	success:(res)=>{
			// 		console.log(res.data.data.rows)
			// 		// this.vipMember=res.data.data.rows
			// 	}
			// })
		},
		methods: {
			// 刷新数据
			onRefresh(){
			   //刷新数据
			   console.log('11')
			},
			// 筛选
			onScreening(){
				// uni.navigateTo({
				// 	url: '../'
				// })
			},
			handletouchmove: function(event) {
						// console.log(event)
						if (this.flag !== 0) {
							return;
						}
						let currentX = event.touches[0].pageX;
						let currentY = event.touches[0].pageY;
						let tx = currentX - this.lastX;
						let ty = currentY - this.lastY;
						this.mindex = -1;
						//左右方向滑动
						if (Math.abs(tx) > Math.abs(ty)) {
							if (tx < 0) {
								this.flag = 1;
								if(this.i == 0){
									this.i = this.vipMember.length-1
								}else{
									this.i -= 1
								}
							} else if (tx > 0) {
								this.flag = 2;
								if(this.i < this.vipMember.length-1){
									this.i += 1
								}else{
									this.i = 0
								}
							}
						}
						//将当前坐标进行保存以进行下一次计算
						this.lastX = currentX;
						this.lastY = currentY;
					},
					handletouchstart: function(event) {
						console.log(event)
						this.lastX = event.touches[0].pageX;
						this.lastY = event.touches[0].pageY;
					},
					handletouchend: function(event) {
						this.flag = 0;
					},
		}
	}
</script>

